}

.container__log-in .content .checkbox + label:before {
  content: "\a ";
  text-align: center;
  vertical-align: middle;
  width: 11px;
  height: 11px;
  background: none;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  margin: -2px 8px 0 0;
  font-family: Verdana;
  font-size: 8px;
  font-weight: bold;
  line-height: 10px;
  display: inline-block;
  position: relative;
}

.container__log-in .content .checkbox:checked + label:before {
